.navbar {
  width: 100%;
  height: 100px;
  background-color: #0F5CBF;
  display: flex;
  flex-direction: row;
  font-family: DM Sans;
  font-size: 16px;
  font: bold;
}
.navbar .leftSide {
  flex: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  padding-left: 60px;
}
.navbar .leftSide img {
  width: 60px;
}
.navbar .leftSide h1{
  padding-left: 5%;
  color: white;
}
.navbar .rightSide {
  flex: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.navbar .rightSide button{
  border: black;
  border-style:groove;
  border-radius: 10px;
  padding-left: 10px;
}
.navbar a {
  color: white;
  text-decoration: none;
  margin: 15px;
}

@media only screen and (max-width: 900px) {
  .navbar .rightSide a {
    color: white;
    text-decoration: none;
    margin: 7px;
  }
  .navbar .leftSide {
    padding-left: 10px;
    font-size: 50%;
  }
}

@media only screen and (max-width: 600px) {
  .navbar .rightSide a {
    display: none;
  }
  .navbar .leftSide h1{
    display: none;
  }
}


